
body { margin:0; font-family:Poppins, sans-serif; background:linear-gradient(135deg,#e0f7fa,#ffffff); color:#222; }
.header { background:linear-gradient(90deg,#81d4fa,#4fc3f7); color:white; display:flex; align-items:center; justify-content:space-between; padding:16px 22px; position:sticky; top:0; z-index:50; }
.logo { display:flex; align-items:center; gap:12px; }
.logo img { width:56px; height:56px; border-radius:10px; }
.logo .brand h1 { margin:0; font-size:20px }
.logo .brand p { margin:0; font-size:12px; opacity:0.9 }
.header nav a { margin-left:12px; color:white; text-decoration:none }
.start-btn, .small-btn { background:#0a2e73; color:#fff; border:none; padding:8px 12px; border-radius:8px; cursor:pointer; }
.small-btn { margin-left:10px; font-size:14px; }
.home-container { text-align:center; padding:40px; }
.home-container .login-box { margin:20px auto; max-width:420px; background:white; padding:20px; border-radius:10px; box-shadow:0 6px 18px rgba(0,0,0,0.06) }
.contribute-page, .courses-page { max-width:1000px; margin:18px auto; padding:12px; }
.contribute-form, .contribute-page .contribute-form { max-width:720px; margin: 14px auto; }
.contribute-form input, .contribute-form textarea { width:100%; padding:10px; margin-top:6px; border-radius:8px; border:1px solid #ddd }
.courses-page .video-container { display:inline-block; border-radius:12px; padding:10px; background:#fff; }
.quiz-section { margin-top:12px; }
.quiz-item { margin-bottom:12px; }
.submit-btn { background:#0056b3; color:white; padding:10px 16px; border-radius:8px; border:none; cursor:pointer }
